.side-menu.open{
    display: inherit;
}
.side-menu.close{
    display: none;
}

.side-menu__layout {
    position: fixed;
    top: 0;
    left:0;
    height: 100vh;
    width:  320px;
    background: white;
    margin-left: 1rem;
    box-shadow: 2rem 0 2rem  rgba(0,0,0,0.3);
    z-index: 2;
}

.side-menu__block {
    margin: 1.3rem 0;
    display: flex;
    justify-content: space-between;
    
}


.side-menu__item{
    padding: .5rem 0;
}
.side-menu__item:hover{
    transform: translateX(10px);
    transition: transform 0.2s ease;
}

.side-menu__item a {
    font-family: var(--text-regular);
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: var(--text-color-black);

}
.side-menu__item--active {
    display: flex;
    margin-left: -16px;
}
.side-menu__item--active a{
    color: var(--text-color-grey);
}

.side-menu__item--active::before{
    content: "";
    display: flex;
    width: 5px;
    height: 28px;
    border-radius: 0 5px 5px 0;
    background-color: var(--color-salat);
    margin-right: 11px;
}

.side-menu__btn{
 margin-bottom: 1rem;
}
.side-menu__mail a{
 margin-bottom: 1rem;
 direction: none;
 color: var(--text-color-black);
}
.side-menu__phone{
 margin-bottom: 1rem;
}

.side-menu__phone a{
direction: none;
 font-family: var(--text-Bold);
font-weight: 700;
font-size: 2rem;
line-height: 32px;
letter-spacing: -0.6px;
color: var(--text-color-black);
}


