
@font-face {  

    font-family: "TTLakesMedium";  
    src: url("../fonts/TTLake/TTLakes-Medium.eot");  
    src: url("../fonts/TTLake/TTLakes-Medium.eot?#iefix")format("embedded-opentype"),  
    url("../fonts/TTLake/TTLakes-Medium.woff") format("woff"),  
    url("../fonts/TTLake/TTLakes-Medium.ttf") format("truetype"); 
    font-style: normal;  
    font-weight:400 500 700; 

}

@font-face {  

    font-family: "TTLakesBold";  
